@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.card {
  position: relative;
  perspective: 800px;
  -moz-perspective: 800px;

  height: var(--card-size);
  width: var(--card-size);

  border-radius: 5px;
  transition: all 0.2s ease;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.02);
}

.card__side {
  position: absolute;
  top: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);

  height: var(--card-size);
  width: var(--card-size);

  transition: all 0.8s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card__side--front {
  border: 8px solid var(--card-border-color);
  background-image: linear-gradient(
    to bottom,
    var(--main-color-light),
    var(--main-color-dark)
  );
  /* transform: rotateY(0deg); */
  font-size: 2em;
  font-weight: bold;
}

.card__side--front:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  box-shadow: 0 8px 11px rgba(0, 0, 0, 0.4);
}

.card__side--back {
  background-color: rgb(255, 255, 255);
  transform: rotateY(180deg);
  z-index: -1;
}

.card.rotated .card__side--front {
  transform: rotateY(180deg);
}

.card.rotated .card__side--back {
  transform: rotateY(360deg);
  overflow: hidden;
}
.card img {
  height: auto;
  border-radius: 5px;
  object-fit: cover;
}

.card__side--front img {
  width: 80%;
}

.card__side--back img {
  width: 90%;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 500px) {
  .card__side--front {
    border: 5px solid var(--card-border-color);
  }
}

.scoreboard {
  grid-column: 1 / 5;
  padding: 0 35px;

  color: white;

  width: 100%;
  height: var(--card-size);
  background-image: linear-gradient(
    to bottom,
    var(--main-color-light),
    var(--main-color-dark)
  );
  border: 10px solid var(--main-color-dark);
  border-radius: 5px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4),
    inset 0 3px 10px rgba(255, 255, 255, 0.8);

  grid-column: 1 / span 4;

  display: flex;
  justify-content: space-between;
  align-items: center;

  overflow: hidden;
}

span {
  display: block;
  text-align: center;
  font-size: 3em;
  font-weight: 900;
  margin-top: -10px;
}

.scoreboard h3 {
  margin-bottom: -10px;
}

.message {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.btn {
  width: 50%;

  margin: 10px 0;
  padding: 10px;

  font-weight: 900;
  color: var(--main-color-dark);

  border-radius: 5px;
  outline: none;
  border: none;
  transition: all 0.2s;
}

.btn:hover {
  cursor: pointer;
  background-color: var(--main-color-dark);
  color: white;
  transform: scale(1.02);
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.8);
}

.btn:active {
  box-shadow: 0 1px 5px rgba(255, 255, 255, 0.8);
  transform: scale(1);
}

/* MEDIA QUERIES */
@media only screen and (max-width: 600px) {
  .scoreboard h3 {
    font-size: 0.9em;
  }
  span {
    font-size: 50px;
  }

  .message h2 {
    height: 1.1em;
    font-size: 20px;
  }

  .btn {
    width: 60%;

    margin: 8px 0;
    padding: 5px;

    font-weight: 900;
  }
}

@media only screen and (max-width: 500px) {
  .scoreboard {
    border: 6px solid var(--main-color-dark);
    padding: 0 20px;
  }

  .scoreboard h3 {
    font-size: 0.8em;
  }

  span {
    font-size: 40px;
  }

  .message h2 {
    height: 1.05em;
    font-size: 18px;
  }

  .btn {
    font-size: 0.7em;
  }
}

@media only screen and (max-width: 430px) {
  .scoreboard h3 {
    font-size: 0.6em;
  }

  span {
    font-size: 30px;
    line-height: 1.7em;
  }

  .message h2 {
    height: 1em;
    font-size: 14px;
  }

  .btn {
    font-size: 0.7em;
    padding: 3px;
  }
}

.app {
  width: 640px;

  display: grid;
  grid-template-columns: repeat(4, var(--card-size));
  grid-template-rows: repeat(4, var(--card-size));
  /* grid-auto-rows: 160px; */
  justify-items: center;
  align-items: center;
  justify-content: center;

  grid-gap: var(--gap-size);

  gap: var(--gap-size);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --gap-size: 20px;
  --card-size: 140px;
  --background-color-dark: rgb(236, 126, 0);
  --background-color-light: rgb(255, 198, 93);
  --main-color-dark: rgb(0, 56, 70);
  --main-color-light: rgb(0, 162, 211);
  --card-border-color: #f1f1f1;
}

html {
  box-sizing: border-box;
}

body {
  font-family: Nunito, sans-serif;
}

#root {
  width: 100vw;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  background-image: linear-gradient(
    to top,
    rgb(236, 126, 0),
    rgb(255, 198, 93));

  background-image: linear-gradient(
    to top,
    var(--background-color-dark),
    var(--background-color-light)
  );
}

@media only screen and (max-width: 670px) {
  :root {
    --card-size: 21vw;
    --gap-size: 2vw;
  }
}

